import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Input from '../../components/uielements/input';
import Checkbox from '../../components/uielements/checkbox';
import Button from '../../components/uielements/button';
import authAction from '../../redux/auth/actions';
import appActions from '../../redux/app/actions';
import IntlMessages from '../../components/utility/intlMessages';
import SignUpStyleWrapper from './signup.style';

const { login, signup } = authAction;
const { clearMenu } = appActions;

class SignUp extends Component {
  state = {
    redirectToReferrer: false,
  };
  componentWillReceiveProps(nextProps) {
    if (
      this.props.isLoggedIn !== nextProps.isLoggedIn &&
      nextProps.isLoggedIn === true
    ) {
      this.setState({ redirectToReferrer: true });
    }
  }
  handleSignUp = () => {
    const { history, signup } = this.props;

    const userInfo = {
      username: document.getElementById('inputUserName').value || '',
      firstname: document.getElementById('inputFirstName').value || '',
      lastname: document.getElementById('inputLastName').value || '',
      email: document.getElementById('inputUserEmail').value || '',
      password: document.getElementById('inpuPassword').value || '',
    };

    signup({ history, userInfo });
  };
  handleLogin = (token = false) => {
    const { login, clearMenu } = this.props;
    console.log(token, 'handlelogin');
    if (token) {
      login(token);
    } else {
      login();
    }
    clearMenu();
    this.props.history.push('/dashboard');
  };
  render() {
    const { history } = this.props;
    console.log('history',history)
    return (
      <SignUpStyleWrapper className="isoSignUpPage">
        <div className="isoSignUpContentWrapper">
          <div className="isoSignUpContent">
            <div className="isoLogoWrapper">
              <Link to="/dashboard">
                <IntlMessages id="page.signUpTitle" />
              </Link>
            </div>

            <div className="isoSignUpForm">
              <div className="isoInputWrapper isoLeftRightComponent">
                <Input id="inputFirstName" size="large" placeholder="First name" />
                <Input id="inputLastName" size="large" placeholder="Last name" />
              </div>

              <div className="isoInputWrapper">
                <Input id="inputUserName" size="large" placeholder="Username" />
              </div>

              <div className="isoInputWrapper">
                <Input id="inputUserEmail" size="large" placeholder="Email" />
              </div>

              <div className="isoInputWrapper">
                <Input id="inpuPassword" size="large" type="password" placeholder="Password" />
              </div>

              <div className="isoInputWrapper">
                <Input
                  id="inpuPasswordConfirm"
                  size="large"
                  type="password"
                  placeholder="Confirm Password"
                />
              </div>

              <div className="isoInputWrapper" style={{ marginBottom: '50px' }}>
                <Checkbox>
                  <IntlMessages id="page.signUpTermsConditions" />
                </Checkbox>
              </div>

              <div className="isoInputWrapper">
                <Button type="primary" onClick={this.handleSignUp}>
                  <IntlMessages id="page.signUpButton" />
                </Button>
              </div>
              <div className="isoInputWrapper isoCenterComponent isoHelperWrapper">
                <Link to="/signin">
                  <IntlMessages id="page.signUpAlreadyAccount" />
                </Link>
              </div>
            </div>
          </div>
        </div>
      </SignUpStyleWrapper>
    );
  }
}

export default connect(
  state => ({
    isLoggedIn: state.Auth.idToken !== null ? true : false,
  }),
  { login, signup, clearMenu }
)(SignUp);
